import React, { Component } from 'react';
import {

    Button,
    Card,
    CardBody,

    CardHeader,
    CardFooter,
    Col,

    FormGroup,
    Form,
    Input,
    InputGroup,
    InputGroupAddon,

    InputGroupText,
  
    Row,
} from 'reactstrap';

class Profile extends Component {
   

    render() {
      const {user} = this.props;
        
        return (
            <div className="animated fadeIn">

                <Row>
                    <Col xs="12" sm="12">
                        <Card>
                            <CardHeader>
                                Profile
              </CardHeader>
                            <CardBody>
                                <Form action="" method="post">
                                    <FormGroup>
                                        <InputGroup>
                                            <InputGroupAddon addonType="prepend">
                                                <InputGroupText>Username</InputGroupText>
                                            </InputGroupAddon>
                                            <Input type="text" disabled={true} value={user.username} id="username" name="username3" autoComplete="name" />
                                            <InputGroupAddon addonType="append">
                                                <InputGroupText><i className="fa fa-user"></i></InputGroupText>
                                            </InputGroupAddon>
                                        </InputGroup>
                                    </FormGroup>
                                    <FormGroup>
                                        <InputGroup>
                                            <InputGroupAddon addonType="prepend">
                                                <InputGroupText>Email</InputGroupText>
                                            </InputGroupAddon>
                                            <Input type="email" value={user.email} disabled={true} id="email3" name="email" autoComplete="username" />
                                            <InputGroupAddon addonType="append">
                                                <InputGroupText><i className="fa fa-envelope"></i></InputGroupText>
                                            </InputGroupAddon>
                                        </InputGroup>
                                    </FormGroup>
                                    <FormGroup>
                                        <InputGroup>
                                            <InputGroupAddon addonType="prepend">
                                                <InputGroupText>Password</InputGroupText>
                                            </InputGroupAddon>
                                            <Input type="password" id="password3" name="password" autoComplete="current-password" />
                                            <InputGroupAddon addonType="append">
                                                <InputGroupText><i className="fa fa-asterisk"></i></InputGroupText>
                                            </InputGroupAddon>
                                        </InputGroup>
                                    </FormGroup>
                                    <FormGroup>
                                        <InputGroup>
                                            <InputGroupAddon addonType="prepend">
                                                <InputGroupText>Confirm Password</InputGroupText>
                                            </InputGroupAddon>
                                            <Input type="password" id="password_conformation" name="password_confirmation" autoComplete="current-password" />
                                            <InputGroupAddon addonType="append">
                                                <InputGroupText><i className="fa fa-asterisk"></i></InputGroupText>
                                            </InputGroupAddon>
                                        </InputGroup>
                                    </FormGroup>
                                    <FormGroup className="form-actions">
                                        <Button type="submit" size="sm" color="primary">Submit</Button>
                                    </FormGroup>
                                </Form>
                            </CardBody>
                            <CardFooter>
                                <p>Change Password?</p>
                            </CardFooter>
                        </Card>
                    </Col>
                </Row>
            </div>
        );
    }
}

export default Profile;
